.cms-landing-page {
  @extend .container-fluid;
  width: 100%;
}
.cms-landing-page-row {
	@extend .row; 
}
.cms-landing-page-row-full-col {
	@extend .col-md-12;
	padding-left: 0px;
	padding-right: 0px;
}
.cms-landing-page-half-col {
	@extend .col-md-6;
	@extend .col-sm-6;
}
.cms-landing-page-quarter-col {
	@extend .col-md-3;
	@extend .col-sm-3; 
}
.cms-landing-page-three-quarter-col {
	@extend .col-md-9;
	@extend .col-sm-9; 
}
