.facets-item-cell-grid {
	@extend .item-cell;
	.cart-add-to-cart-button-container {
		width:100%;
	}
	.cart-add-to-cart-button-button {
		width: auto !important;
		padding: 10px 20px;
	}
}

.facets-item-cell-grid-quick-view-wrapper {
	@extend .item-cell-quick-view-wrapper;
	//*custom
    background-color: #f9a225;
    border-radius: 4px;
}

.facets-item-cell-grid-quick-view-link {
	@extend .item-cell-quick-view-link;
    background-color: #f9a225;
}

.facets-item-cell-grid-image-wrapper {
	position:relative;
	text-align:center;
}

// .facets-item-cell-grid-link-image {}

// .facets-item-cell-grid-quick-view-icon {}

// .facets-item-cell-grid-stock {}


.facets-item-cell-grid-details {
	text-align:left;
	.product-line-stock {
		text-align:left;
	}
}

.facets-item-cell-grid-image {
	//@extend .item-cell-image;
	//width:auto;
	//height:auto;
	width: 100%;
	height: 160px;
	display: block;
	object-fit: contain;
	margin: 0 auto;
}

.facets-item-cell-grid-title {
	@extend .item-cell-title;
	margin-bottom: $sc-margin-lv2;
	//*custom
    font-weight: bold;
    color: rgb(7, 54, 116);
    
}

.facets-item-cell-grid-price {
	@extend .small-price;
	margin-bottom:$sc-margin-lv1;
}

.facets-item-cell-grid-rating {
	text-align:left;
	display: inline-block;
}

.facets-item-cell-grid {
	.global-views-star-rating-area {
		margin-bottom:$sc-margin-lv1;
	}
}

.facets-item-cell-grid-stock-message {
	text-align:left;
}


//TT new styles

div.facets-items-collection-view-cell-span3 {
	padding: 0;
	div.facets-item-cell-grid {
		padding: $sc-padding-lv1 * 3;
		// margin-bottom: $sc-padding-lv1 * 5;
		margin-bottom: 0;
		border: none;
	}
	.facets-item-cell-grid-details{
		> div:empty{display:none;}
	}
	// Item Image
	div.facets-item-cell-grid-image-wrapper {
		//border: 1px solid $sc-color-theme-border-light;
		padding: $sc-padding-lv1 * 2;
		min-height:185px;
		border: 1px solid $theme-border-colors-light;
		a { display: block; }
	}
	// Item name
	h2.facets-item-cell-grid-title {
        min-height: 70px;
		a { 
			@extend .cells-item-name; 
			min-height: 75px;
			@media (max-width: $screen-lg-min) { min-height: 90px; }
			@media (max-width: $screen-sm-min) { min-height: 75px; }
			@media (max-width: $screen-xs-min) {
				font-size: $sc-font-size-s !important;
				line-height:normal;
				min-height: 90px;
			}
		}
		@media (max-width: $screen-md-min) { 
			//padding-top: 0; 
		}
	}
	// Item SKU & MPN
	div.item-details-sku-container,
	div.item-details-mpn-container {
		margin: 0 0 $sc-padding-lv1 0;
		padding-right: $sc-padding-lv1;
		text-transform: uppercase;
		font-weight: $sc-font-weight-semibold;
		line-height: $sc-font-size-s;
		font-size: $sc-font-size-s;

		span.item-details-sku,
		span.item-details-mpn {
			font-weight: $sc-font-weight-normal;
		}
	
		span { font-weight: $sc-font-weight-semibold; }
	}
	// Item Price
	span.item-views-price-lead { @extend .cells-item-price; }
	// Quick View btn.
	div.facets-item-cell-grid-quick-view-wrapper {
		a { padding: $sc-padding-lv1 * 2 $sc-padding-lv1; }
	}
	// Rating
	div.facets-item-cell-grid-rating {
		i {
			color: $sc-color-star-filled;
			&.global-views-star-rating-empty { opacity: 0.25; }
		}
	}
	// Stock
	div.facets-item-cell-grid-stock {
		> div { height: 55px; }
	}
}

