.facets-item-cell-table {
	@extend .clearfix;
	@extend .item-cell;

	.global-views-star-rating {
		float:none;
	}

	.product-details-option-color {
		text-align:center;
	}

	.product-details-facets-option-color {
		text-align:center;
	}

	.cart-quickaddtocart-price {
		text-align:left;
		margin-bottom:$sc-margin-lv2;
	}

	// .cart-quickaddtocart {
	// 	text-align:center;
	// }

	.cart-add-to-cart-button-container {
		width:100%;
		padding:0px;

		.cart-add-to-cart-button-button {
			margin-top: 0;
			width: auto;
			padding: 10px 20px;

			@media (max-width: $screen-md-min) {
				width: 100%;
				padding: 10px 0px;
			}
		}
	}

	.product-views-option-facets-color {
		text-align:center;
	}
}

.facets-item-cell-table-quick-view-wrapper {
	@extend .item-cell-quick-view-wrapper;
}

.facets-item-cell-table-quick-view-link {
	@extend .item-cell-quick-view-link;
}

.facets-item-cell-table-image-wrapper {
	position:relative;
	text-align:center;

	@media (min-width: $screen-xs-min)
	{
		width: 50%;
		float: left;
		padding: 0 $sc-margin-lv3;
	}
}

.facets-item-cell-table-content-wrapper {
	word-break: break-all;
	@media (min-width: $screen-xs-min)
	{
		width: 100%;
		float: left;
		display: block;
		padding-left: $sc-margin-lv2;
	}
}

// .facets-item-cell-table-link-image {}

// .facets-item-cell-table-quick-view-icon {}

// .facets-item-cell-table-add-to-cart-itemid {}

.facets-item-cell-table-stock {
	text-align:left;
}

.facets-item-cell-table-details {
	text-align:left;
}

.facets-item-cell-table-image {
	width:100%;
	height:auto;
	display: block;
}

.facets-item-cell-table-title {
	@extend .item-cell-title;
}

.facets-item-cell-table-price {
	@extend .small-price;
	margin-bottom:$sc-margin-lv2;

	.product-views-price {
		text-align:left;
	}
}

.facets-item-cell-table-rating {
	text-align:left;
}

.facets-item-cell-table {
	.global-views-star-rating-area {
		margin-bottom:$sc-margin-lv2;
	}
	.global-views-star-rating-container {
		width:auto;
		display:table;
	}
}

.facets-item-cell-table-add-to-cart-quantity {
	@extend .input-medium;
}

.facets-item-cell-table-add-to-cart-button {
	@extend .button-primary;
	@extend .button-medium;
}

.facets-item-cell-table-stock-message {
	text-align:left;
}

//TT new style

div.facets-items-collection-view-cell-span6 {
	padding: 0;
   div.facets-item-cell-table {
	   width: 100%;
	   padding: $sc-padding-lv1 * 3;
	   border: none;
	   margin-bottom: $sc-padding-lv1 * 5;
   }
   // Item Image
   div.facets-item-cell-table-image-wrapper {
	   border: 1px solid $sc-color-theme-border-light;
	   padding: $sc-padding-lv1 * 2;
	   margin-right:14px;
	   margin-bottom:20px;
	   a { display: block; }
	   @media (min-width: 480px){
		   width:40%;
		   margin-bottom:40px;
	   }
   }
   // Item name
   h2.facets-item-cell-table-title {
	   padding: $sc-padding-lv1 0;
	   min-height:inherit;
	   a {
		   @extend .cells-item-name; 
		   padding: 0;
		   @media (max-width: $screen-lg-min) { min-height: 90px; }
		   @media (max-width: $screen-sm-min) { min-height: 75px; }
		   @media (max-width: $screen-xs-min) {
			   font-size: $sc-font-size-s !important;
			   min-height: 90px;
		   }
	   }
	   @media (max-width: $screen-xs-min) { line-height: 12px; }
	   @media (min-width: 480px){
		   padding:0 0 $sc-padding-lv1 * 3 0;
	   }
   }
  	// Item SKU & MPN
	div.item-details-sku-container,
	div.item-details-mpn-container {
		display: inline-block;
		margin: 0 0 $sc-padding-lv1 0;
		padding-right: $sc-padding-lv1;
		text-transform: uppercase;
		font-weight: $sc-font-weight-semibold;
		line-height: $sc-font-size-s;
		font-size: $sc-font-size-s;

		span.item-details-sku,
		span.item-details-mpn {
			font-weight: $sc-font-weight-normal;
		}
	
		span { font-weight: $sc-font-weight-semibold; }
		
		@media (max-width: $screen-xs-min) { display: block; }
	}
	div.item-details-mpn-container {
		@media (min-width: $screen-xs-min) {
			border-left: 1px solid $sc-color-copy;
			padding-left: $sc-padding-lv1 + 4px;
		}
	}
   // Item price
   span.item-views-price-lead { @extend .cells-item-price; }
   // Quick View btn.
   div.facets-item-cell-table-quick-view-wrapper {
	   a {
		   margin: 0 auto;
		   max-width: 155px;
	   }
   }
   // Stock
   div.facets-item-cell-list-stock {
	   text-align: center;
	   > div {
	   height: $sc-padding-lv1 * 8;
		   .item-views-stock-msg-out { margin: 0; }
	   }
   }
   // Rating
   div.facets-item-cell-table-rating {
	   float: none;
	   display: inline-block;
	   i {
		   color: $sc-color-star-filled;
		   &.global-views-star-rating-empty { opacity: 0.25; }
	   }
	   // Filled
	   div.global-views-star-rating-area-filled { text-align: right; }
   }
}



div.facets-items-collection-view-cell-span6 {
	padding: 0;
   div.facets-item-cell-table {
	   width: 100%;
	   padding: $sc-padding-lv3;
	   border: none;
	   margin-bottom: $sc-padding-lv5;
   }
   // Item Image
   div.facets-item-cell-table-image-wrapper {
	   border: 1px solid $sc-color-theme-border-light;
	   padding: $sc-padding-lv2;
	   margin-right: 0;
	   margin-bottom: 0px;
	   a { display: block; }


	   @media (min-width: 480px){
		   width: 100%;
		   margin-bottom: 26px;
	   }
   }
   // Item name
   h2.facets-item-cell-table-title {
	   padding: $sc-padding-lv3 0;
	   min-height:inherit;
	   a {
		   @extend .cells-item-name; 
		   padding: 0;
		   @media (max-width: $screen-lg-min) { min-height: 90px; }
		   @media (max-width: $screen-sm-min) { min-height: 75px; }
		   @media (max-width: $screen-xs-min) {
			   font-size: $sc-font-size-s !important;
			   min-height: 90px;
		   }
	   }
	   @media (max-width: $screen-xs-min) { line-height: 12px; }
	   @media (min-width: 480px){
		   padding:0 0 $sc-padding-lv1 * 3 0;
	   }
   }
	// Item SKU
	div.item-details-sku-container {
		margin: 0 0 $sc-padding-lv1 0;
		text-transform: uppercase;
		font-weight: $sc-font-weight-semibold;
		font-size: $sc-font-size-s;
		span.item-details-sku { font-weight: $sc-font-weight-normal; }
	}
   // Item price
   span.item-views-price-lead { @extend .cells-item-price; }
   // Quick View btn.
   div.facets-item-cell-table-quick-view-wrapper {
	   a {
		   margin: 0 auto;
		   max-width: 155px;
	   }
   }
   // Stock
   div.facets-item-cell-list-stock {
	   text-align: center;
	   > div {
	   height: $sc-padding-lv1 * 8;
		   .item-views-stock-msg-out { margin: 0; }
	   }
   }
   // Rating
   div.facets-item-cell-table-rating {
	   float:none; display:inline-block;
	   i {
		   color: $sc-color-star-filled;
		   &.global-views-star-rating-empty { opacity: 0.25; }
	   }
	   // Filled
	   div.global-views-star-rating-area-filled { text-align: right; }
   }
}

.facets-item-cell-table{
	.facets-item-cell-table-price{
		float:none!important; display:block!important;
		span{float:none!important; display:block!important;}
	}
	.facets-item-cell-table-stock, .brief-description-content, div[data-view="StockDescription"]{float:none!important; display:block!important;}
}




